import React,{useState,useEffect} from 'react'
import Card from 'react-bootstrap/Card'
import Form from 'react-bootstrap/Form'
import Container from 'react-bootstrap/Container'
import Button from 'react-bootstrap/Button'
import auth from '../firebase/firebase'
import Firebase from 'firebase'
function Login(props) {
	const handleCardClose=()=>{
		props.handleLogin()
	}
	const [email,setEmail]=useState('')
	const [password,setPassword]=useState('')

    const handleEmail=(event)=>{
    	setEmail(event.target.value)
    }
    const handlePassword=(event)=>{
    	setPassword(event.target.value)
    }

	const handleSubmit=(event)=>{
       event.preventDefault();
       console.log("Login")
       Firebase.auth().signInWithEmailAndPassword(email, password).catch(function(error) {
        // Handle Errors here.
        var errorCode = error.code;
        var errorMessage = error.message;
        // [START_EXCLUDE]
        if (errorCode == 'auth/wrong-password') {
          alert('The password is wrong.');
        } else {
          alert(errorMessage);
        }
        console.log(error);
        // [END_EXCLUDE]
      });
	}
	return(
		<Container className="container">
         <Card bg="info">
         <Card.Header>
         Enter Login Credentails
         <Button onClick={handleCardClose} variant="warning"><i className="material-icons">close</i></Button> 
         </Card.Header>
         <Card.Body>
        <Form onSubmit={handleSubmit}>
         <Form.Group controlId="formBasicEmail">
         <Form.Label>Email address</Form.Label>
         <Form.Control type="email" placeholder="Enter email" defaultValue={email} onChange={handleEmail} required/>
         <Form.Text className="text-muted">
            We'll never share your email with anyone else.
         </Form.Text>
         </Form.Group>

         <Form.Group controlId="formBasicPassword">
         <Form.Label>Password</Form.Label>
         <Form.Control type="password" placeholder="Password" defaultValue={password} onChange={handlePassword} required/>
         </Form.Group>
         <Form.Group controlId="formBasicCheckbox">
         <Form.Check type="checkbox" label="Check me out" />
         </Form.Group>
         <Button variant="primary" type="submit">
            Submit
         </Button>
         </Form>
         </Card.Body>
         </Card>
        </Container>
		)
}

export default Login