import React,{useState,useEffect} from "react"
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Image from 'react-bootstrap/Image'
import axios from "axios"
import './body.css'
function Body(){
	const [products,setProducts]=useState([])

	useEffect(()=>{
        axios.get("products.json")
        .then((response)=>{
        	setProducts(response.data)
        	console.log(response)
        })
        .catch((error)=>{
        	console.log(error)
        })
	},[])
	return(
           <div className="ml-0 pl-0 mr-0 pr-0">
           <div className="logo mt-0 ">
                <Image src="logo.png"  fluid/> 
           </div>
           <div className="margin-container">
           <Container>
           <span className="mt-0 ml-5 title text-center body-container">Products</span>
           {
           	products.map((product)=>(
           		      
           		       <a href="#" key={product.id} ><Image src={product.imgthumb} className="mb-5 ml-5" rounded fluid /></a>
           		       
           		))
           }
           </Container>
           </div>
           </div>
		) 
}
export default Body